$xl: 1440px;
$lg: 992px;
$md: 768px;
$sm: 576px;
$xs: 0;

$font-heading: "Antic Didone", serif;
$font-body: "Noto Sans KR", sans-serif;

html,
body {
  width: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
  font-family: $font-body;
  font-weight: 100;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: $font-heading;
}

/***** Navigation *****/
.navbar {
  transition: 500ms ease;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  .navbar-brand {
    font-family: $font-heading;
    text-transform: uppercase;
  }
  li a {
    font-family: $font-heading;
  }
  .button:active,
  button:active,
  .button:focus,
  button:focus,
  .button:hover,
  button:hover {
    outline: none;
  }
  .navbar-toggler {
    border: none;
    .bar {
      display: inline-block;
      cursor: pointer;
    }
    .bar1,
    .bar2,
    .bar3 {
      width: 20px;
      height: 1px;
      background-color: rgba(255, 255, 255, 0.8);
      margin: 6px 0;
      transition: 0.4s;
    }
    .menuChange .bar1 {
      transform: rotate(-45deg) translate(-3px, 6px);
      background-color: #fff;
    }
    .menuChange .bar2 {
      opacity: 0;
    }

    .menuChange .bar3 {
      transform: rotate(45deg) translate(-3px, -8px);
      background-color: #fff;
    }
  }
}

.navbar.scrolled {
  background-color: rgba(32, 37, 41, 0.9);
}

.darkMenu {
  background: rgba(32, 37, 41, 0.9);
}

/***** Jumbotron *****/
.jumbotron {
  color: #fff;
  padding: 10rem 2rem 8rem;
  background-image: linear-gradient(
      rgba(32, 37, 41, 0.9),
      rgba(32, 37, 41, 0.9)
    ),
    url("../images/wang-kenan-nADyJKsjfx4-unsplash.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  h1 {
    text-transform: uppercase;
    font-weight: 300;
    font-size: 4rem;
    letter-spacing: 5px;
    @media (max-width: $md) {
      font-size: 3rem;
    }
    @media (max-width: $sm) {
      font-size: 2rem;
    }
  }
  hr {
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    width: 250px;
  }
  a {
    font-size: 1rem;
  }
  .btn {
    border-radius: 0;
  }
}

/***** About *****/
#about {
  padding: 6rem 3rem 5rem;
  img {
    max-width: 80%;
    border-radius: 50%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  .about-me {
    margin-top: 3rem;
