svg {
  width: 300px;
  height: auto;
  padding-top: 2rem;
}

.overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(32, 37, 41, 0.9);
  overflow: hidden;
  width: 0;
  height: 100%;
  transition: 0.5s ease;
}

.overlayContainer:hover .overlay {
  width: 100%;
}

.overlayText {
  position: absolute;
  left: 100%;
  right: 0%;
  top: 50%;
  transform: translate(-50%, -50%);
  white-space: nowrap;
  transition: 0.8s ease;
}

.overlayText a {
  color: #fff;
  text-decoration: none;
  transition: 0.3s ease;
}

.overlayText a:hover {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.5);
  border-left: solid rgba(255, 255, 255, 0.5);
  border-right: solid rgba(255, 255, 255, 0.5);
  padding: 2px 15px;
}

.overlayContainer:hover .overlayText {
  left: 50%;
}

/***** Portfolio - page *****/
#portfolio-page {
  padding: 3rem 0 6rem;
  .myPortfolio {
    margin-top: 10rem;
    margin-bottom: 5rem;
    padding: 6rem 0 5rem;
    @media (max-width: $md) {
      padding: 6rem 2rem 5rem;
    }
  }
  img {
    width: 500px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  a {
    font-size: 0.8rem;
  }
}

/***** Contact *****/
#contact {
  color: #fff;
  /* min-height: 1000px; */
  padding-top: 30rem;
  background-image: linear-gradient(
      rgba(32, 37, 41, 0.7),
      rgba(32, 37, 41, 0.7)
    ),
    url("../images/kiwihug-264572-unsplash.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  .social {
    height: 300px;
    background-color: rgba(32, 37, 41, 0.9);
    ul {
      padding: 0;
      margin-top: 9rem;
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      list-style-type: none;
      a {
        font-size: 4rem;
        color: rgba(255, 255, 255, 0.5);
        transition: 0.5s ease;
        &:hover {
          color: #fff;
        }
        @media (max-width: $md) {
          font-size: 3rem;
        }
        @media (max-width: $sm) {
          font-size: 2.5rem;
        }
      }
    }
  }
}

